<template>
    <el-main>
      <el-row :gutter="20">
        <el-col :span="6"><div class="grid-content bg-purple">
          <el-input v-model="input" placeholder="请输入内容"></el-input>
          </div></el-col>
        <el-col :span="6"><div class="grid-content"></div></el-col>
        <el-col :span="6"><div class="grid-content "></div></el-col>
      </el-row>

      <el-table :data="tableData">
        <el-table-column prop="order_id" label="ID" width="140">
        </el-table-column>
        <el-table-column prop="order_sn" label="订单编号" width="120">
        </el-table-column>
        <el-table-column prop="user_name" label="用户名称" width="120">
        </el-table-column>
        <el-table-column prop="consignee" label="收货人">
        </el-table-column>
        <el-table-column prop="total_count" label="实付金额">
        </el-table-column>
        
        <el-table-column label="操作">
           <template slot-scope="scope">
            <router-link :key="scope.row.user_id" :to="{path:'/ordersinfo',query:{order_id:scope.row.order_id}}"> 查看订单详情</router-link>
          </template>
        </el-table-column>
      </el-table>


          

      <el-pagination
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-count="pageCount"
      :page-size="pageSize"
      layout="total, prev, pager, next, jumper"
      :total="tableNum">
    </el-pagination>
    </el-main>
    
</template>


<script>
  export default {
       data() {
          return {       
            tableData:[],
            input:'',
            currentPage: 1,
            tableNum:1,
            pageCount:1,
            pageSize:1,
            
          }

        },
        //初始加载
       created(){
           this.shoplist()
       },
       methods:{
         //商品列表
          shoplist(page=1) {
           this.$axios({
             method:'post',
             url:this.url() +'/index.php/orders/list',
             params :{
               page:page,
             }
           
           }).then(res=>{
              //this.tableData.push(this.form);
              //this.tableData=this.form;
             res.data.data.list.forEach(result => {
               if(result.is_on_sale==1){
                  //console.log(result.is_on_sale);   
                 result.is_on_sale='是';
               }else{
                 result.is_on_sale='否';
               }              
             });   
              this.tableData=res.data.data.list;
              this.currentPage=res.data.data.paginate.current_page;
              this.tableNum=res.data.data.paginate.record_count;
              this.pageSize=res.data.data.paginate.page_size;
              this.pageCount=res.data.data.paginate.page_count;
              //console.log(res.data.data.paginate);   

            });     
         },
        
          //分页
          handleCurrentChange(val) {
            this.shoplist(val);
            //console.log(`当前页: ${val}`);
          }
       
      }


  }
</script>

<style>
 
  .el-row {
    margin-bottom: 20px;
  }
  .el-col {
    border-radius: 4px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .bg-purple {
    background: #d3dce6;
  }
  .bg-purple-light {
    background: #e5e9f2;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
</style>


